import React from 'react'
import { Doughnut } from 'react-chartjs-2'


let data = {
  labels: ['   ', ' '],
  datasets: [
    {
      label: '# ',
      data: [12, 19],
      backgroundColor: [
        'rgba(255, 99, 132, 0.2)',
        'rgba(54, 162, 235, 0.2)',
        'rgba(255, 206, 86, 0.2)',
        'rgba(75, 192, 192, 0.2)',
        'rgba(153, 102, 255, 0.2)',
        'rgba(255, 159, 64, 0.2)',
      ],
      borderColor: [
        'rgba(255, 99, 132, 1)',
        'rgba(54, 162, 235, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(153, 102, 255, 1)',
        'rgba(255, 159, 64, 1)',
      ],
      borderWidth: 1,
    },
  ],
}

const DoughnutChart = ({dataArr}) => {
    // console.log('dataArr',dataArr);
    data.datasets[0].data = dataArr;
    data.labels[0] = '    (' + dataArr[0] +')';
    data.labels[1] = '  (' + dataArr[1] +')';

    return (
        <>
          <Doughnut data={data} />
        </>
      )
}

export default DoughnutChart